/*libs*/
/* main */
* {
  -webkit-backface-visibility: hidden;
}

body {
  overflow-y: scroll;
  overflow-x: hidden;
}

body.active {
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.wrapper .main {
  flex: 1 1 auto;
}

.container {
  width: 1195px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%;
}
@media (max-width: 768px) {
  .container {
    padding: 0 4vw;
  }
}

.swiper-pagination-current::after {
  content: "f";
  text-transform: lowercase;
  opacity: 0;
}

.swiper-pagination-total::before {
  content: "f";
  text-transform: lowercase;
  opacity: 0;
}

.general-title {
  display: inline-flex;
  align-items: center;
  font-family: "Muller";
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 120%;
  color: #101010;
  position: relative;
}
@media (max-width: 1100px) {
  .general-title {
    font-size: 36px;
  }
}
@media (max-width: 768px) {
  .general-title {
    font-size: 6.4vw;
  }
}
.general-title img {
  margin-right: 10px;
  position: relative;
  bottom: 5px;
}
@media (max-width: 768px) {
  .general-title img {
    margin-right: 1.333vw;
    width: 3.2vw;
    height: 5.8vw;
    bottom: 0.4vw;
  }
}
.general-title-line {
  position: absolute;
  top: 50%;
  left: calc(100% + 40px);
  height: 1px;
  background-color: #D24318;
}
@media (max-width: 768px) {
  .general-title-line {
    left: calc(100% + 2.667vw);
    height: 0.267vw;
  }
}

.show {
  opacity: 0;
  transition: opacity 1s ease 0s;
}

.show.active {
  opacity: 1;
}

.ibg {
  position: relative;
}
.ibg img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

