/* ---------hugge-------------hugge--------- */
.hugge {
  background-color: #F6F6F6;
  padding: 88px 0 104px;
  position: relative;
  /*&__popap-container.active {
  	transition: opacity 0.3s ease 0s;
  	opacity: 1;
  	pointer-events: auto;
  	overflow: auto;
  }*/
}
@media (max-width: 768px) {
  .hugge {
    padding: 10.667vw 0;
    overflow: visible;
  }
}
.hugge__overflow {
  overflow: hidden;
}
.hugge__popap-back {
  cursor: pointer;
  position: fixed;
  z-index: 101;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.4);
  transition: opacity 0.3s ease 0s, top 0.3s ease 0.3s;
  pointer-events: none;
}
.hugge__popap-back.active {
  transition: opacity 0.3s ease 0s;
  top: 0;
  opacity: 1;
  pointer-events: auto;
}
@media (max-width: 768px) {
  .hugge__popap-back.active {
    z-index: 96;
  }
}
.hugge__popap-wrapper {
  top: 0;
  left: 0;
  position: fixed;
  overflow: auto;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease 0s;
  pointer-events: none;
  opacity: 0;
  z-index: 102;
}
.hugge__popap-wrapper.active {
  opacity: 1;
  pointer-events: auto;
}
@media (max-width: 768px) {
  .hugge__popap-wrapper {
    z-index: 97;
    padding: 7.6vw 0 0 0;
    top: 0 !important;
    left: 0;
    transform: translate(0%, 0%) !important;
    background-color: #ffffff;
  }
}
.hugge__popap-container {
  max-height: 100%;
  opacity: 1;
  padding: 0;
  z-index: 102;
  width: 965px;
  max-width: 100%;
  overflow: auto;
}
@media (max-width: 768px) {
  .hugge__popap-container {
    position: relative;
    z-index: 97;
    overflow-x: hidden;
  }
}
.hugge__popap-container::-webkit-scrollbar {
  width: 0px;
}
.hugge__popap-words {
  position: relative;
  background-color: #ffffff;
  height: 100%;
  display: flex;
}
@media (max-width: 768px) {
  .hugge__popap-words {
    flex-direction: column;
  }
}
.hugge__popap-words span {
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 12px;
  width: 32px;
  height: 32px;
  display: inline-block;
  background-color: #ffffff;
  border-radius: 50%;
  z-index: 99;
}
@media (max-width: 768px) {
  .hugge__popap-words span {
    width: 8.533vw;
    height: 8.533vw;
    top: 4vw;
    right: 4vw;
  }
}
.hugge__popap-words span::before, .hugge__popap-words span::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 16px;
  height: 2px;
  background-color: #636363;
}
@media (max-width: 768px) {
  .hugge__popap-words span::before, .hugge__popap-words span::after {
    width: 4.267vw;
  }
}
.hugge__popap-words span::before {
  left: 25%;
  transform: rotate(45deg);
}
.hugge__popap-words span::after {
  right: 25%;
  transform: rotate(-45deg);
}
.hugge__popap-img {
  flex: 0 0 339px;
  background: url("../images/hugge/img-popap.jpg") center/cover no-repeat;
  transform: scale(1.005);
}
@media (max-width: 768px) {
  .hugge__popap-img {
    flex: 0 0 65.067vw;
    background: url("../images/hugge/img-popap-mobile.jpg") 0 0/cover no-repeat;
  }
}
.hugge__popap-body {
  padding: 20px 20px 30px 62px;
  font-style: normal;
  font-family: "Muller";
  display: flex;
  flex-direction: column;
  overflow-x: auto;
}
@media (max-width: 900px) {
